

/* Variabili CSS */
:root {
    --background-home: #2c3e50; /* Colore di sfondo della home */
    --text-home: #ecf0f1; /* Colore del font della home */
    --border-color-image: #ffffff; /* Colore del bordo dell'immagine */
    --topbar-background: rgba(30, 30, 30, 0.8); /* Colore della top bar */
    --button-topbar-background: #3498db; /* Colore di sfondo dei bottoni della top bar */
    --button-topbar-hover: #2980b9; /* Colore di sfondo dei bottoni della top bar al passaggio del mouse */
    --button-topbar-font: #ffffff; /* Colore del font dei bottoni della top bar */
}