@import '../../styles/colors.css';

body, html {
    margin: 0;
    padding: 0;
    font-family: Arial, sans-serif;
}

.home {
    position: relative;
    height: 100vh;
    width: 100vw;
    overflow: hidden;
    /* contenuto centrato verticalmente */
    display: flex;
    justify-content: center;
    align-items: center;
    /* contenuto centrato orizzontalmente */
    text-align: center;
}

.background-color {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    /*background: linear-gradient(to bottom right, #4A90E2, #9013FE); /* Gradiente blu e viola */
    background-color: var(--background-home);
    z-index: 1; /* Assicurati che lo sfondo sia dietro gli altri contenuti */
}

