@import '../../styles/colors.css';



.app-bar{
    /*posizionata sopra in alto*/
    margin: 5px 20px !important;
    margin-bottom: 0 !important;
    padding: 0 !important;
    position:fixed !important;
    top: 0 !important;
    left: 0 !important;
    
    width: calc(100% - 40px) !important; /* Sottrai i margini per mantenere la larghezza */
    height: 60px;



    /* contenuto centrato verticalmente */
    display: flex !important;
    justify-content: space-between !important;

    /* Colore di sfondo scuro con trasparenza */
    /*background: rgba(30, 30, 30, 0.8) !important; /* Sfondo grigio scuro con 80% di opacità */
    background: var(--topbar-background) !important; /* Colore di sfondo della top bar */
    backdrop-filter: blur(5px) !important;  /* Aggiunge un effetto di sfocatura */
    -webkit-backdrop-filter: blur(10px) !important; /* Aggiunge un effetto di sfocatura per Safari */

    /* Ombra e bordi */
    /*box-shadow: 0px 4px 1px rgba(0, 0, 0, 0.5) !important; /* Ombra più marcata */
    border-radius: 10px !important;

    z-index: 10;
}
.logo {
    height: 40px !important; 
    width: auto !important; 
    display: flex !important; 
    align-items: center !important; 
    background-color: azure !important;
    margin: 0 !important; 
    padding: 0 !important; 
    /* Assicurati di non avere una larghezza fissa qui */
}

  
.nav-buttons-container {
    display: flex !important;
    align-items: center !important;
    margin: 0 40px !important;
    padding: 0 !important;
}
  
.nav-button {
    margin: 0 !important;
    margin-left: 10px !important; /* Aggiunto uno spazio tra i pulsanti */
    /*transition: color 0.3s ease-in-out;*/
    /*transition: background-color 0.3s ease-in-out, color 0.3s ease-in-out !important;*/
    padding: 8px 12px !important; /* Aggiungi padding per un aspetto più uniforme */
    border-radius: 5px; /* Aggiungi bordi arrotondati */
    background-color: var(--button-topbar-background) !important; /* Colore di sfondo rosso per i pulsanti */
    color: var(--button-topbar-font) !important; /* Colore del testo dei pulsanti */
}
  
.nav-button:hover {
    background-color: var(--button-topbar-hover) !important; /* Colore di sfondo quando si passa il mouse */
    color: var(--button-topbar-font) !important; /* Mantiene il colore del testo bianco */
}

.tool-bar {
    margin: 0 !important; /* Assicurati di annullare i margini */
    padding: 0 20px !important; /* Assicurati di annullare i padding */
    display: flex !important; /* Display flex */
    justify-content: space-between !important; /* Allinea gli elementi agli estremi */
    align-items: center !important; /* Centra verticalmente i contenuti della barra */
    height: 100%; /* Assicura che l'altezza del toolbar sia al 100% */
    width: 100%;
}